import Login from '@vdian/login'

function isLogin() {
  return Login.isLogin()
}

function goLogin() {
  const url = Login.login({
    environment: '2',
    device: 1
  })
  window.location.href = window.spider.transferActionBy(url, {
    actionName: '登录'
  })
}

export { isLogin, goLogin }



